<template>
  <div>
    <div class="row">
      <div class="col-sm-12">
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title" v-if="users && users.length > 0">قائمة المستخدمين - {{ users.length }}</h4>
            <h4 class="card-title" v-else>قائمة المستخدمين - 0</h4>
          </template>

          <template v-slot:body>
            <div class="mt-3 inputs mb-3">
              <i class="material-symbols-outlined">
                search
              </i>
              <input type="text" class="form-control" placeholder=" ... ابحث عن مستخدم" v-model.trim="searchModel"
                v-on:keyup="searchByName(searchModel)" />
            </div>
            <div class="table-responsive" v-if="users && users.length > 0">
              <table id="datatable" class="table table-striped table-bordered">
                <thead>
                  <tr>
                    <th></th>
                    <th>اسم المتخدم</th>
                    <th>حساب الفيسبوك</th>
                    <th>البريد الالكتروني</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(user, index) in users.slice(0, length)" :key="index">
                    <td class="align-middle text-center">
                      <span class="align-middle material-symbols-outlined user-status users-status-on"
                      :class="[user.is_active == 1 ? 'user-status-on' : 'user-status-off']">
                      account_circle
                      </span>
                    </td>
                    <td>
                      {{ user.name }}
                    </td>
                    <td>
                      {{ user.fb_name }}
                    </td>
                    <td>
                      {{ user.email }}
                    </td>
                    <td>
                      <router-link class="text-center" :to="{
                        name: 'user.update-name',
                        params: { user_id: user.id },
                      }">
                        <i class="icon material-symbols-outlined">
                          edit
                        </i>
                      </router-link>
                    </td>
                  </tr>
                </tbody>
              </table>
              <span class="w-100 text-center me-3 btn" role="button" @click="loadMore()" v-if="users.length > length">
                عرض المزيد
              </span>

            </div>

            <div v-else>
              <h4>
                لا يوجد مستخدمين
                <span class="material-symbols-outlined align-middle">
                  info
                </span>
              </h4>
            </div>
          </template>
        </iq-card>

      </div>
    </div>
  </div>
</template>
<script>
import userServices from '@/api/userServices'

export default {
  name: "Groups List",
  async created() {
    const users = await userServices.getAll();
    this.users = users;
  },
  data() {
    return {
      users: [],
      searchModel: "",
      length: 10,
      user_status: [
        'غير فعال',
        'فعال',
        'بحاجة لتعديل ملومات',
      ]
    };
  },

  methods: {
    async searchByName(name) {
      let users = null;
      if (name != '') {
        users = await userServices.searchByName(name);
      }
      else {
        users = await userServices.getAll();
      }
      this.users = users;
    },
    loadMore() {
      this.length += 10;
    },
  },
};


</script>

<style lang="scss" scoped>
.inputs {
  position: relative;
}

.form-control {
  text-indent: 15px;
  border: none;
  height: 45px;
  border-radius: 0px;
  border-bottom: 1px solid #eee;
}

.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #eee;
  outline: 0;
  box-shadow: none;
  border-bottom: 1px solid blue;
}

.form-control:focus {
  color: blue;
}

.inputs i {
  position: absolute;
  top: 14px;
  left: 4px;
  color: #b8b9bc;
}
.user-status {
  font-variation-settings:
    'FILL' 1, 'wght' 400, 'GRAD' 0, 'opsz' 48
}
.user-status-off {
  color: darkred;
}
.user-status-on {
  color: green;
}
</style>
